*::selection {
  color: initial;
  background-color: initial;
}
body {
  margin: 0;
  font-family: inherit;
}
.header {
  background-color: orange;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  width: 100%;
  box-sizing: border-box;
  padding: 10px;
}
.header_icon {
  font-size: 2rem !important;
}

.header > span {
  font-size: 25px;
  letter-spacing: 2px;
  font-weight: 900;
  color: white;
  padding-left: 10px;
}
.header > small {
  align-self: flex-end;
  font-weight: 700;
  font-variant: small-caps;
  letter-spacing: 1px;
}
.footer {
  background-color: orange;
  text-align: center;
  position: fixed;
  bottom: 0;
  width: 100%;
  font-weight: 900;
  color: white;
  font-family: "Courier New", Courier, monospace;
}
.create-note-component {
  display: flex;
  justify-content: center;
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: white;
  padding: 1rem;
}
form {
  position: relative;
  box-shadow: 2px 2px 5px black;
  display: flex;
  flex-direction: column;
  padding: 10px;
  resize: both;
  min-width: 200px;
  min-height: 100px;
  max-width: 90%;
  max-height: 80vh;
  overflow: auto;
  border-radius: 10px;
  transition: 0.3s all;
}
form > input {
  outline: none;
  border: 0;
  font-size: 20px;
  font-weight: 900;
  border-bottom: 1px solid gray;
  text-transform: capitalize;
}
form > textarea {
  outline: none;
  border: 0;
  text-transform: capitalize;
  resize: none;
}
.addBtn,
.deleteBtn,
.editBtn {
  border: 0;
  font-size: 25px;
  font-weight: 900;
  position: absolute;
  bottom: 10px;
  right: 10px;
  outline: none;
  color: orange;
  background-color: white;
  border-radius: 50px;
  width: 40px;
  box-shadow: 2px 2px 10px black;
  cursor: pointer;
  transition: 0.5s all;
}
.deleteBtn {
  bottom: -10px;
  right: -10px;
}
.editBtn {
  top: -10px;
  right: -10px;
}
.addBtn:hover,
.deleteBtn:hover,
.editBtn:hover {
  background-color: rgb(247, 211, 144);
  color: white;
  box-shadow: 0 0 3px 1px gold inset;
}
.addBtn:disabled {
  box-shadow: none;
  border: 1px solid gold;
  color: gold;
  background-color: gainsboro;
  cursor: not-allowed;
}
.addBtn:disabled:hover {
  color: gold;
}
.note_container {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 2rem;
}
.notes {
  position: relative;
  margin: 20px;
  min-width: 200px;
  width: 200px;
  text-transform: capitalize;
  box-shadow: 2px 2px 10px black;
  padding: 10px;
  display: inline-block;
  border-radius: 10px;
  flex-grow: 1;
}

.notes > h1 {
  margin: 0;
  border-bottom: 1px solid gray;
  overflow: hidden;
  text-overflow: ellipsis;
  font-size: 1.4rem;
}
.notes > p {
  text-align: justify;
  padding: 5px;
  word-break: break-all;
}
@media screen and (max-width: 519px) {
  .notes {
    width: 82%;
  }
}
